@import url(https://fonts.googleapis.com/css?family=Lato:700|Montserrat|Open+Sans);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

video {
  width: 100%;
  height: 100vh;
  object-fit: fill;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.6);
}

.z1 {
  z-index: 1;
}

#row {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}

#column {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

#horizontal {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

#vertical {
  display: -webkit-flex;
  display: flex;
  justify-items: center;
}

.alignStart {
  display:-webkit-flex;
  display:flex;
  -webkit-align-items: flex-start;
          align-items: flex-start
}

.whiteText {
  color: white;
}

.mt20 {
  margin-top: 20px
}
.container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  width: 94%;
  padding: 40px;
  position: absolute;
  top: 15%;
  z-index: 1; }

.city {
  color: white;
  z-index: 1; }
  .city--climate {
    font-size: 34px;
    font-weight: 700;
    font-family: 'Lato'; }
  .city--city {
    font-size: 22px;
    font-weight: 300; }
  .city--temperature {
    font-size: 44px;
    font-weight: 800; }
  .city--title {
    font-size: 18px;
    font-weight: 400; }
  .city--value {
    font-size: 22px;
    font-weight: 600; }
  .city--coordinates {
    font-size: 14px;
    font-weight: 300; }

.cityWrap {
  color: white;
  z-index: 1; }

.climate_icon {
  z-index: 1;
  width: 130px;
  margin-left: -20px;
  margin-top: -20px; }
  .climate_icon--small {
    width: 28px;
    margin-right: 15px; }

.mt10 {
  margin-top: 10px; }

.mb20 {
  margin-bottom: 20px; }

.ml25 {
  margin-left: 25px; }

.mr25 {
  margin-right: 25px; }

input:focus {
  color: white; }

input {
  color: white; }

.search {
  z-index: 3;
  position: absolute;
  top: 10%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 50%; }

.search_input {
  width: 100%;
  border-top-color: transparent;
  border-left-color: transparent;
  border-right-color: transparent;
  background: transparent;
  border-bottom-color: white;
  border-bottom-width: 0.5px;
  padding: 5px;
  height: 30px; }
  .search_input--text {
    font-size: 16px;
    font-weight: 600;
    letter-spacing: 1px; }

.search_icon {
  width: 30px;
  cursor: pointer;
  margin-left: 10px; }

.search_icon svg path {
  fill: white; }

::-webkit-input-placeholder {
  /* Edge */
  color: white; }

::-ms-input-placeholder {
  color: white; }

::placeholder {
  color: white; }

.footerContainer {
  position: absolute;
  left: 50%;
  bottom: 20%;
  background: rgba(255, 255, 255, 0.2);
  padding-right: 25px;
  z-index: 2;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%); }

.footer_icon {
  z-index: 1;
  width: 30px;
  margin-right: 10px; }

.alignCenter {
  -webkit-align-items: center;
          align-items: center; }

footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 40px;
  color: white;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  background: rgba(0, 0, 0, 0.6);
  z-index: 1; }

